import React from 'react';
import HomePage from '../HomePage/HomePage';
import LogIn from '../LogIn/LogIn';
import CreateAccount from '../CreateAccount/CreateAccount';
import CreateAccount1 from '../CreateAccount/CreateAccount1';
import CreateAccount2 from '../CreateAccount/CreateAccount2';
import CreateAccount3 from '../CreateAccount/CreateAccount3';
import CreateAccount4 from '../CreateAccount/CreateAccount4';
import Dashboard from '../Pages/Dashboard';
import PasswordConfirmation from '../CreateAccount/PasswordConfirmation';
import PoliticaPrivacidade from './PoliticaPrivacidade';
import TermosDeServicos from './TermosDeServicos';
import TransferPage from '../Wallet/TransferPage';
import MarketValue from '../MarketValue/MarketValue';
import SapiensBalance from '../Pages/SapiensBalance';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import SacarPage from '../Wallet/SacarPage';
import InviteFriends from '../Wallet/InviteFriends';
import EsqueciSenha from '../LogIn/EsqueciSenha';
import Settings from '../Settings/Settings';
import Notifications from '../Notefications/Notifications';
import Chat from '../Chat/Chat';
import Sugestions from '../Sugestions/Sugestions';
import NeutralizationSpots from '../NeutralizationSpots/NeutralizationSpots';
import Neutralization from '../Neutralization/Neutralization';


function Rotas() {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/" component={HomePage} />
        <Route exact path="/login" component={LogIn} />
        <Route exact path="/create-account" component={CreateAccount} />
        <Route exact path="/create-account/1" component={CreateAccount1} />
        <Route exact path="/password-confirmation" component={PasswordConfirmation} />
        <Route exact path="/create-account/2" component={CreateAccount2} />
        <Route exact path="/create-account/3" component={CreateAccount3} />
        <Route exact path="/create-account/4" component={CreateAccount4} />
        <Route exact path="/dashboard" component={Dashboard} />
        <Route exact path="/privacy-policy" component={PoliticaPrivacidade} />
        <Route exact path="/services-terms" component={TermosDeServicos} />
        <Route exact path="/transfer" component={TransferPage} />
        <Route exact path="/sacar" component={SacarPage} />
        <Route exact path="/market-value" component={MarketValue} />
        <Route exact path="/balance" component={SapiensBalance} />
        <Route exact path="/invite-friends" component={InviteFriends} />
        <Route exact path="/recover-password" component={EsqueciSenha} />
        <Route exact path="/settings" component={Settings} />
        <Route exact path="/notifications" component={Notifications} />
        <Route exact path="/chat" component={Chat} />
        <Route exact path="/sugestions" component={Sugestions} />
        <Route exact path="/neutralization-spots" component={NeutralizationSpots} />
        <Route exact path="/neutralization" component={Neutralization} />
      </Switch>
    </BrowserRouter>
  );
}

export default Rotas;
