import React from 'react';
import { Link } from 'react-router-dom'
import setaVoltarBranca from '../imagesIcons/setaVoltarBranca.svg';
import '../Layout/Style.css'

const BasicHeader = ({username, title}) => {
  return (
    <header className="app-header-2 green-background">
    <div className="seta-voltar">
      <Link to="/dashboard">
          <img className="icon-size-1" src={setaVoltarBranca}/>
      </Link>
    </div>
    <div className="header-2-info-container">
      <p>{username}</p>
      <p className="text-size-2"><strong>{title}</strong></p>
    </div>  
    <div className="filler-div"><p> </p></div>
  </header>
  )
}

export default BasicHeader;