import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom';
import MenuInferior from '../Components/MenuInferior';
import WalletMenu from './Components/WalletMenu';
import Header from './Components/Header';
import { getValueByKey, sum } from '../services/utils';
import '../Layout/Style.css';


const NEUTRALIZACAO = 0;
const COMPENSACAO = 1;
const COMPRA = 2;
const DISPONIVEL = 3;

const SapiensBalance = ({ history }) => {
  
  const loginData = getValueByKey('loginData');
  /* const { username, wallet: { balance } } = loginData */
  const [soma, setSum ] = useState('emptySum')
  const [username1, setUsername] = useState('name')
  const [balance1, setBalance] = useState('balance')
  
  useEffect(() => {
    if (loginData === 'emptyData') {
      history.push("/")
    }
  },[loginData])

  useEffect(() => {
    if (loginData !== 'emptyData') {
      const { username, wallet: { balance } } = loginData
      setUsername(username)
      setBalance(balance)
      setSum(sum(loginData));
    }
  },[])

  return (
    <div className="balance-content-container">

      <Header username={ username1 } to="/dashboard">
        <div style={{ textAlign: 'center', fontSize:'24px', color: 'var(--light-grey)' }}>
          SPN {parseFloat(soma).toFixed(2)}
        </div>
      </Header>

      <div className="balance-info-container">
        <div className="balance-title">
        <p className="black-text"><strong>Resumo da conta</strong></p>
        </div>
        <div className="balance-info blue-border">
        <div className="balance-dot"/><p className="darkGrey-text">Por plano de <strong>NeutralizaÃ§Ã£o</strong></p>
          <p className="blue-text text-size-3">SPN {balance1[NEUTRALIZACAO]}</p>
        </div>
        <div className="balance-info green-border">
          <div className="balance-dot"/><p className="darkGrey-text">Por neutralizaÃ§Ã£o <strong>ecolÃ³gica</strong></p>
          <p className="green-text text-size-3">SPN {balance1[COMPENSACAO]}</p>
        </div>
        <div className="balance-info orange-border">
        <div className="balance-dot"/><p className="darkGrey-text">Sapiens-coins <strong>disponÃ­veis</strong></p>
          <p className="orange-text text-size-3">SPN {parseFloat(balance1[DISPONIVEL]).toFixed(2)}</p>
        </div>
        <div className="balance-info">
          <p className="darkGrey-text"><strong>TransaÃ§Ãµes</strong></p>
          <p className="darkGrey-text">Adquiridas</p>
          <p>SPN {/* {balance[COMPRA]} */}</p>
          <p className="darkGrey-text">Enviadas</p>
          <p>SPN {/* {balance[COMPRA]} */}</p>
        </div>

      </div>

      <WalletMenu />

      <MenuInferior />
      
    </div>
  )
}

export default withRouter(SapiensBalance);