import React, {useEffect, useState} from 'react';
import { withRouter } from 'react-router-dom';
import { isConnected } from '../services/fetch';
import { clearStorage, initStorage } from '../services/utils';
import '../App.css';

const HomePage = ({ history }) => {
  const [serverStatus, setServerStatus] = useState(
    <span style={{ color: 'grey' }}>Loading...</span>
  );

  // On component did mount
  useEffect(() => {
    isConnected().then(({ status }) => {
      setServerStatus(
        status === 'ok' ?
          <span style={{ color: 'green' }}>Online</span> :
          <span style={{ color: 'red' }}>Offline</span>
      );
    });
    clearStorage();
    initStorage();
  }, []);

  return (
      <div className="screen home">
        <div />
        <div className="s-container">
          <div className="title-home-container">
            <p className="title">
              <strong>Com a sapiens vocÃª neutraliza o seu impacto ecolÃ³gico no planeta!</strong>
            </p>
          </div>
          <button
            className="s-btn s-green"
            type="button"
            onClick={() => history.push("/create-account")}
          >
          Cadastrar
          </button>
          <button
            className="s-btn s-white"
            type="button"
            onClick={ () => history.push('/login') }
          >
            Entrar
          </button>
        </div>
        <small className="text-white">API: { serverStatus }</small>
      </div>
  );
};

export default withRouter(HomePage);
