
const API_URL = 'https://sapiens-test.herokuapp.com/';

const fakeAccount = {
  email: 'luis.codemaker@gmail.com',
  password: '123456',
};

const methodCreate = (body, method) => ({
  method: method || 'POST',
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
  },
  body: JSON.stringify(body)
});

const fetchAPI = (url, data = {}) =>
  fetch(url, data)
    .then((response) =>
      response
        .json()
        .then((data) => data)
        .catch((err) => {
          console.error(err);
          return {};
        })
    )
    .catch((err) => {
      console.error(err);
      return {};
    });

export const isConnected = () => fetchAPI(API_URL);

export const register = async ({
    username, /* Nome completo - [string] */
    cellnumber, /* NÃºmero do telefone - [number] */
    email, /* Email - [string] */
    cpf, /* CPF - [number] */
    birthday, /* AniversÃ¡rio - [dd-mm-aaaa] */
    password, /* Senha - [string] */
    cpassword, /* ConfirmaÃ§Ã£o de senha - [string] */
}) => (
  await fetchAPI(`${API_URL}api/register`,
    methodCreate(
      { username, cellnumber, email, cpf, birthday, password, cpassword }
    )
  )
);

export const login = async ({
    email, /* Email - [string] */
    password, /* Email - [string] */
}) => await fetchAPI(`${API_URL}api/login`, methodCreate({ email, password }));

export const getUserData = async ({ 
  accessToken, /* Token de acesso - [string] */
}) => await fetchAPI(`${API_URL}api/user`, methodCreate({ accessToken }));

export const updateUserData = async ({
    accessToken, /* Token de acesso - [string] */
    data, /* Objeto com as chaves a serem atualizadas, ex. { username: 'Maria' } */
}) => await fetchAPI(`${API_URL}api/updateuser`, methodCreate({ accessToken, data }));

export const sendTokens = async ({
  accessToken, /* Token de acesso - [string] */
  password, /* Senha - [string] */
  to, /* CPF do destinatÃ¡rio - [string] */
  amount, /* Quantidade de tokens - [float] */
}) => await fetchAPI(`${API_URL}api/transfer`, methodCreate(
  { accessToken, password, to, amount }
));

export const sacarTokens = async ({
  accessToken, /* Token de acesso - [string] */
  addr, /*wallet - [string] */
  password, /* Senha - [string] */
  amount, /* Quantidade de tokens - [float] */
}) => await fetchAPI(`${API_URL}api/withdraw`, methodCreate(
  { accessToken, password, addr, amount }
));

export const confirmationCode = async ({
  email, /* email do novo usuario - [string] */
  code, /* Codigo de 4 digitos enviado por email ou sms - [string] */
}) => await fetchAPI(`${API_URL}api/confirm`, methodCreate({ email, code }));

