export const removeUser = async ({
  accessToken, /* Token de acesso - [string] */
}) => await fetchAPI(`${API_URL}api/user`, methodCreate({ accessToken }, 'DELETE'));

export const fetchPriceInfo = async () => {
  const result = await fetch('https://sapiens-test.herokuapp.com/api/sapiens/prices').then(response => response.json())
  return result;
}


