import React, { useState, useEffect } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { getUserData, fetchPriceInfo } from '../services/fetch'
import { getValueByKey, setValueByKey, sum } from '../services/utils';
import MenuInferior from '../Components/MenuInferior';
import CardMenu from './Components/Card';
import Header from './Components/Header';

import graphIcon from '../imagesIcons/graphIcon.svg'
import extratoNeutraBranco from '../imagesIcons/extratoNeutraBranco.svg';

import 'materialize-css';
import '../App.css';
import M from 'materialize-css';

const menuLabelStyle = {
  background: 'var(--primary)',
  boxShadow: 'none',
  borderBottomLeftRadius: '15px',
  borderBottomRightRadius: '15px',
  marginTop: '-5px',
};

const Menu = ({ children, isOpen }) => (
  <li style={{ color: 'var(--light-grey)' }}>
    <div className="collapsible-body">
      { children }
    </div>
    <div
      className="collapsible-header"
      style={ menuLabelStyle }
    >
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          width: '100%',
        }}
      >
        <a className="btn-floating btn-small" style={{ background: 'var(--green-light)' }}>
          <i className="material-icons">
            keyboard_arrow_{ isOpen ? 'up' : 'down' }
          </i>
        </a>
      </div>
    </div>
  </li>
);

const Item = ({ children, to }) => (
  <div style={{
    border: 'none',
    color: 'var(--light-grey)',
    display: 'flex',
    justifyContent: 'space-between',
    width: '100%',
    }}>
      <span style={{ fontSize: '1.5rem' }}>
      <div style={{ display: 'flex', alignItems: 'center' }}>
      { children }
      </div>
      </span>
      <Link to={ to }>
      <i
      className="small material-icons"
      style={{ color: 'var(--light-grey)' }}
      >chevron_right</i>
      </Link>
  </div>
);


const fetchUserData = async (accessToken) => {
  const result = await getUserData({ accessToken });
  return result;
}

const Dashboard = ({ history }) => {

  const token = getValueByKey('loginToken');
  const loginData = getValueByKey('loginData');
  const [menuStatus, setMenuStatus] = useState(false);
  const [soma, setSum] = useState('emptySum');
  const [variation, setSapiensVariation] = useState('0.00');
  const { username } = loginData;

  useEffect(() => {

    const menu = document.querySelector('.collapsible');
    const menuInstance = M.Collapsible.init(menu, {
      onOpenStart: () => { setMenuStatus(true) },
      onCloseStart: () => { setMenuStatus(false) },
    });

    if (token === 'empty') history.push('/');
    else {
      const { data } = token;
      console.log(data.accessToken);
      fetchUserData(data.accessToken)
        .then((result) => {
          setValueByKey('loginData', result.data);
          setSum(sum(result.data));
        });
      fetchPriceInfo()
        .then((response) => {
          const { SPN } = response.data;
          return setSapiensVariation(parseFloat(((SPN[0]/SPN[1]) - 1) * 100).toFixed(2));
        })
        .finally(() => { menuInstance.open(); });
    }
  }, [])

  return(
    <div
      className="s-white"
      style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}
    >
      <Header username={username}>
        <div style={{ width: '100%' }}>
          <ul
            className="collapsible"
            style={{
              border: 'none',
              boxShadow: 'none',
              marginBottom: '-15px',
            }}
          >
           <Menu isOpen={ menuStatus }>
              <Item to="/balance">SPN {parseFloat(soma).toFixed(2)}</Item>
              <hr />
              <Item to="/market-value">
                <img className="icon-size-2 white1-background rounder-icon" src={graphIcon} />
                <span
                  className="dashboard-text"
                  style={{ display: 'flex', padding: '0 16px' }}
                >
                  {variation} %
                </span>
                <i className="material-icons">arrow_drop_up</i>
              </Item>
              <hr />
              <Item to="/neutralization">
                <img className="icon-size-2 white1-background rounder-icon" src={extratoNeutraBranco} />
                <span
                  className="dashboard-text"
                  style={{ fontSize: '12px' }}
                >
                  Neutralizado: 500 kg de diÃ³xido carbono.
                </span>
              </Item>
           </Menu>
          </ul>
        </div>
      </Header>

      <div className="screen">
        <CardMenu />
      </div>
      <MenuInferior />
    </div>
  )
}

export default withRouter(Dashboard);