
import React, { useEffect, useState} from 'react'
import { Link, withRouter } from 'react-router-dom'
import MenuInferior from '../Components/MenuInferior';
import { getValueByKey, showMessage } from '../services/utils';
import setaVoltarBranca from '../imagesIcons/setaVoltarBranca.svg'
import BasicHeader from '../Components/BasicHeader'

/* Fazer validacao do email, alem disso Ã© preciso um endpoint */


const InviteFriends = ({ history }) => {
  
  const loginData = getValueByKey('loginData')
  const { username } = loginData

  const [validEmail, setValidEmail] = useState('');
  const [email1, setEmail1] = useState('');

  const validateEmail = (email) => {
    const re = /\S+@\S+\.\S+/;
    setValidEmail(re.test(email));
    setEmail1(email);
  }

  useEffect(() => {
    if (loginData === 'emptyData') {
      history.push("/")
    }
  },[])

  return(
    <div className="content-container black-background">
      
      {/* <header className="app-header-2 green-background">
        <div className="seta-voltar">
          <Link to="/dashboard">
              <img className="icon-size-1" src={setaVoltarBranca}/>
          </Link>
        </div>
        <div className="header-2-info-container">
          <p>{username}</p>
          <p className="text-size-2"><strong>Convidar Amigos</strong></p>
        </div>  
        <div><p> </p></div>
      </header> */}

      <BasicHeader title="Convidar Amigos" username={username} />


      <div className="form-container">
      <div className="text-container">
        <p>Juntos neutralizamos mais</p>
      </div>
        <input
          className="create-account-input darkGrey-background"
          type="email"
          placeholder="E-mail do amigo" 
          onChange={(event) => validateEmail(event.target.value)}
          />
        <input
          className="create-account-input darkGrey-background"
          type="number"
          placeholder="Whatsapp do amigo"
          />
        <input
          className="create-account-input darkGrey-background"
          type="password"
          placeholder="Senha"
          /* password confirmation event */
          />
        <button
        className="create-account-btn green-background white-text"
        type="button"
        onClick={() => {
          if (!validEmail) {
            return showMessage('Escreva um email vÃ¡lido', 'ERROR')
          }
          if (validEmail) {
            /* email setting  */
          }
        }}
        >Confirmar</button>
      </div>

      <div id="thewarning"></div>

      <MenuInferior />

    </div>
  )
}

export default withRouter(InviteFriends);