
import React, { useEffect } from 'react'
import { Link, withRouter } from 'react-router-dom'
import MenuInferior from '../Components/MenuInferior';
import { getValueByKey, showMessage } from '../services/utils';
import { sacarTokens } from '../services/fetch';
import { useState } from 'react';
import setaVoltarBranca from '../imagesIcons/setaVoltarBranca.svg';
import BasicHeader from '../Components/BasicHeader'
import '../Layout/Style.css';
import './Wallet.css'

const sacarSapiens = async (accessToken, addr, password, amount) => {
  const result = await sacarTokens({accessToken, addr, password, amount})
  return result;
}

const SacarPage = ({ history }) => {
  
  const loginData = getValueByKey('loginData')
  const token = getValueByKey('loginToken')
  const { username } = loginData
  const { data: { accessToken } } = token
  const [cryptowallet, setCryptoWallet] = useState('empty');
  const [amount, setAmount] = useState(0);
  const [input, setInput] = useState('empty');
  const [response, setResponse] = useState('empty')

  useEffect(() => {
    if (loginData === 'emptyData') {
      history.push("/")
    }
  },[])


  return(
    <div className="content-container black-background">

      <BasicHeader title="Sacar Sapiens-coin" username={username} />
      
      <div className="form-container">
      <div className="text-container">
        <p>Sacar para uma Wallet pessoal</p>
      </div>
        <input
          className="create-account-input darkGrey-background"
          type="text"
          placeholder="NÃºmero da carteira" 
          onChange={(event) => setCryptoWallet(event.target.value)}
        />
        <input
          className="create-account-input darkGrey-background"
          type="number"
          placeholder="Quantidade"
          onChange={(event) => setAmount(event.target.value)}
        />
        <input
          className="create-account-input darkGrey-background"
          type="password"
          placeholder="Senha"
          onChange={(event) => setInput(event.target.value)}
        />
        <button
          className="create-account-btn green-background white-text"
          type="button" 
          onClick={() => {
            sacarSapiens(accessToken, input, cryptowallet, amount)
            .then((result) => {
              const { status, errors } = result
              status === 'ok' & errors.length === 0
              ? showMessage('Saque realizado com sucesso', 'SUCCESS')
              : errors.map(error => showMessage(error, 'ERROR'))
          })}}
        >
          Confirmar
        </button>
      </div>

      <MenuInferior />

    </div>
  )
}

export default withRouter(SacarPage);