import React from 'react';
import '../Layout/Style.css';
import './MarketValue.css';

const MVCard = ({coin}) => {
  const { name, priceR, icon, priceD, variation } = coin
  return (
    <div className="currency-card">
      <div className="card-image">
        <img src={icon} alt="cryptocurrency icon" className="icon-size-1"/>
      </div>
      <p className="black-text card-text"><strong>{name}</strong></p>
      <p className="grey-text card-text">US$ {priceD}</p>
      <p className="green-text card-text">R$ {priceR}</p>
      <p className="black-text card-text"><strong>{variation}%</strong></p>
    </div>
  )
}

export default MVCard;
