import React from 'react';
import { useEffect } from 'react';
import { useState, useContext } from 'react';
import { withRouter, Link } from 'react-router-dom';
import UserContext from '../Context/UserContext';
import { login } from '../services/fetch';
import { setValueByKey, getValueByKey, showMessage } from '../services/utils';
import '../App.css';

import Footer from '../Components/Footer';
import Header from '../Components/Header';

import confirmado from '../imagesIcons/confirmado.svg'

export const fetchUser = async (email, password) => {
  const result = await login({ email,  password });
  return result;
}

const LogIn = ({ history }) => {

  const { justRegistered } = useContext(UserContext);
  const [email1, setEmail1] = useState('');
  const [password1, setPassword1] = useState(0);
  const [token, setToken] = useState(getValueByKey('loginToken'));
  
  useEffect(() => {
    console.log(token);
    if (token !== 'empty') {
      return handleResults()
    }
  }, [token]);
  
  const handleResults = () => {
    console.log(token)
    if (token.errors.length > 0) {
      showMessage(token.errors[0] || token.errors[1], 'ERROR')
    } else {
      history.push('/dashboard')
    }
  }

  const confirmRegistration = () => {
    if (justRegistered) {
      return (
        <div className="just-register-container">
          <p><strong>Cadastro confirmado !</strong></p>
          <img className="icon-size-2" src={confirmado} />
          <p>Acesse sua conta agora:</p>
        </div>
      )
    }
  }
  
  return (
    <div className="screen" style={{ backgroundColor: 'black' }}>

      <Header />

      { () => confirmRegistration() }
    
      <div className="s-container">
        <input
          className="s-input s-grey"
          type="text"
          placeholder="Email, nome de usuÃ¡rio ou telefone"
          onChange={(event) => setEmail1(event.target.value)}
        />
        <input
          className="s-input s-grey"
          type="password"
          placeholder="Senha"
          onChange={(event) => setPassword1(event.target.value)}
        />
        <button
          className="s-btn s-green"
          type="button"
          onClick={async () => {
            await fetchUser(email1, password1)
              .then((result) => {
                setValueByKey('loginToken', result)
              }
            );
            setToken(getValueByKey('loginToken'));
          }}
        >
        Entrar
        </button>
        <div>
          <Link to="/recover-password">
            <p>Esqueci minha senha</p>
          </Link>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default withRouter(LogIn);
