import React, { useContext, useState } from 'react';
import { withRouter } from 'react-router-dom';
import UserContext from '../Context/UserContext';
import { showMessage } from '../services/utils'

import Footer from '../Components/Footer';
import Header from '../Components/Header';

import '../App.css';

const CreateAccount2 = ({ history }) => {

  const { setEmail } = useContext(UserContext);
  const [validEmail, setValidEmail] = useState('');
  const [email1, setEmail1] = useState('');
 

  const validateEmail = (email) => {
    const re = /\S+@\S+\.\S+/;
    setValidEmail(re.test(email));
    setEmail1(email);
  }

  return (
    <div className="screen" style={{ backgroundColor: 'black' }}>
      <Header to="/create-account/1" />
      <div className="form-container">
        <div className="text-container">
          <strong>Informe seu e-mail</strong>
          <p>Precisamos do seu e-mail. Vamos usÃ¡-lo para enviar informaÃ§Ãµes importantes a vocÃª</p>
        </div>
        <input
          className="s-input s-white"
          type="email"
          placeholder="email"
          onChange={(event) => validateEmail(event.target.value)}
        />
        <button
          className="s-btn s-green"
          type="button" 
          id="submit-btn" 
          onClick={() => {
            if (!validEmail) {
              showMessage('Escreva um email vÃ¡lido', 'ERROR')
            }
            setEmail(email1)
            history.push("/create-account/3")
          }}
        >
          AvanÃ§ar
        </button>
      </div>
      <Footer />
    </div>
  );
};

export default withRouter(CreateAccount2);
