
import React, { useEffect } from 'react'
import { useState } from 'react';
import { Link, withRouter } from 'react-router-dom'
import MenuInferior from '../Components/MenuInferior';
import { sendTokens } from '../services/fetch';
import { getValueByKey, showMessage } from '../services/utils';
import setaVoltarBranca from '../imagesIcons/setaVoltarBranca.svg';
import BasicHeader from '../Components/BasicHeader';

const Transfer = async (accessToken, password, to, amount) => {
  console.log('entrou no fetch')
  return await sendTokens({ accessToken, password, to, amount })
}

const TransferPage = ({ history }) => {
  
  const loginData = getValueByKey('loginData')
  const token = getValueByKey('loginToken')
  const { username, wallet: {balance} } = loginData;

  const [friendCPF, setFriendCPF] = useState('empty');
  const [amount, setAmount] = useState(0);
  const [input, setInput] = useState('empty');
  const [response, setResponse] = useState('empty')

  const { data: { accessToken } } = token;

  useEffect(() => {
    if (loginData === 'emptyData') {
      history.push("/")
    }
  },[])

  return(
    <div className="content-container black-background">

      <BasicHeader title="Transferir Sapiens-coin" username={username} />
      
      <div className="form-container">
      <div className="text-container">
        <p>Transferir para contas Sapiens</p>
      </div>
        <input 
          className="create-account-input darkGrey-background"
          type="text"
          placeholder="CPF do beneficiÃ¡rio" 
          onChange={(event) => setFriendCPF(event.target.value)}
          /* evento de verificacao de cpf */
          />
        <input
          className="create-account-input darkGrey-background"
          type="number"
          placeholder="Quantidade"
          step="0.01"
          onChange={(event) => setAmount(event.target.value)}
          /* evento de verificacao do saldo */
          />
        <input
          className="create-account-input darkGrey-background"
          type="password"
          placeholder="Senha"
          onChange={(event) => setInput(event.target.value)}
          /* evento de verificacao da senha */
          />
        <button 
          className="create-account-btn green-background white-text"
          type="button"
          onClick={
            () => {
              if (friendCPF.length !== 11) {
                return showMessage('Digite um cpf vÃ¡lido', 'ERROR')
              }
              if(amount > balance[3]) {
                return showMessage('Saldo insuficiente', 'ERROR')
              }
              Transfer(accessToken, input, friendCPF, amount)
              .then((result) => setResponse(result))
              .then(console.log)
              showMessage('Transferencia realizada com sucesso', 'SUCCESS')
            }}
          >
            Confirmar
          </button>
      </div>

      <MenuInferior />

    </div>
  )
}

export default withRouter(TransferPage);

