import React from 'react';
import { Link } from 'react-router-dom';
import 'materialize-css';
import '../App.css';

export default ({ color, to }) => (
  <header className="app-header" style={{ color: `var(--${color || 'primary'})` }}>
    <Link to={`${ to || '/' }`} style={{ display: 'flex', alignItems: 'center' }}>
      <i className="material-icons left">chevron_left</i>Voltar
    </Link>
  </header>
);
