import React from 'react';
import { Link } from 'react-router-dom';
import Icon from './IconItem';

import sacarSapiens from '../../imagesIcons/sacarSapiens.svg'
import transferirSapiens from '../../imagesIcons/transferirSapiens.svg'
import carrinhoCompras from '../../imagesIcons/carrinhoCompras.svg'

const style = {
  width: '100%',
  display: 'flex',
  justifyContent: 'center',
  padding: '32px 0',
};

const WalletMenu = () => {
  return (
    <div style={style}>
      <Icon to="/sacar" title="Sacar Sapiens-coin" icon={sacarSapiens} />
      <Icon to="/tranfer" title="Transferir Sapiens-coin" icon={transferirSapiens} />
      <Icon to="/" title="Neutralizar meu impacto" icon={carrinhoCompras} />
    </div>

  )
}

export default WalletMenu;