import React, { useContext, useEffect } from 'react';
import { useState } from 'react';
import { Link, withRouter } from 'react-router-dom';
import UserContext from '../Context/UserContext';
import { confirmationCode } from '../services/fetch';
import { showMessage } from '../services/utils';
import '../App.css';
import reenviarCodigo from '../imagesIcons/reenviarCodigo.svg';


const getConfirmation = (code, email) => {
  const response = confirmationCode({ code, email })
  return response;
}

const PasswordConfirmation = ({ history }) => {

  const { userCellNumber, userEmail, setJustRegistered } = useContext(UserContext);
  const [input, setInput] = useState('');
  const [confirmationResponse, setConfirmationResponse] = useState('empty')

  useEffect(() => {
    console.log(confirmationResponse);
    if (confirmationResponse !== 'empty') {
      return handleResults();
    }
  }, [confirmationResponse]);

  const handleResults = () => {
    if (confirmationResponse.errors.length > 0) {
      showMessage(confirmationResponse.errors[1] || confirmationResponse.errors[0], 'ERROR')
    }
    if (!confirmationResponse.errors.length) {
      setJustRegistered(true)
      history.push('/login')
    };
  }

  return (
    <div className="screen" style={{ backgroundColor: 'black' }}>

      <div className="form-container">

        <div className="text-container">
          <p>Para continuar, digite aqui o cÃ³digo de quatro dÃ­gitos que te enviaremos por SMS no nÃºmero {`${userCellNumber}`}</p>
        </div>

        <input 
          className="s-input s-white" 
          type="number" 
          placeholder="XXXX" 
          minLength="4" 
          maxLength="4" 
          onChange={(event) => setInput(event.target.value)}
          style={{ width: '120px' }}
        />
        <button 
          className="s-btn s-green"
          type="button"
          onClick={() => {
            getConfirmation(input, userEmail)
            .then((response) => setConfirmationResponse(response));
          }}
        >
          AvanÃ§ar
        </button>
        <div style={{ color: 'var(--primary)', display: 'flex' }}>
          <img className="icon-size-4" src={reenviarCodigo} />
          Reenviar cÃ³digo
        </div>
        
        <div>
          <p>
            <Link to="/create-account">Editar nÃºmero</Link> ou <a href="#">Preciso de ajuda</a>
          </p>
        </div>
      </div>
      
    </div>
  );
};

export default withRouter(PasswordConfirmation);
