import React from 'react'
import { Link } from 'react-router-dom'
import sacarSapiens from '../imagesIcons/sacarSapiens.svg'
import transferirSapiens from '../imagesIcons/transferirSapiens.svg'
import carrinhoCompras from '../imagesIcons/carrinhoCompras.svg'

const WalletMenu = () => {
  return (
    <div className="wallet-STC-menu">
      <div className="wallet-STC-menu-item">
        <Link to="/sacar">
          <img className="icon-size-1 rounder-icon" src={sacarSapiens} />
        </Link>
        <p className="black-text item-text">Sacar Sapiens-coin</p>
      </div>
      <div className="wallet-STC-menu-item">
        <Link to="/transfer">
        <img className="icon-size-1 rounder-icon" src={transferirSapiens} />
        </Link>
        <p className="black-text item-text">Transferir Sapiens-coin</p>
      </div>
      <div className="wallet-STC-menu-item">
        <img className="icon-size-1 rounder-icon" src={carrinhoCompras} />
        <p className="black-text item-text">Neutralizar meu impacto</p>
      </div>
    </div>

  )
}

export default WalletMenu;