import React, { useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom';
import MenuInferior from '../Components/MenuInferior';
import { getValueByKey, sum } from '../services/utils'
import setaVoltarBranca from '../imagesIcons/setaVoltarBranca.svg'
import extratoNeutraBranco from '../imagesIcons/extratoNeutraBranco.svg'
import sino from '../imagesIcons/sino.svg'
import '../Layout/Style.css'

const Neutralization = ({ history }) => {

  const loginData = getValueByKey('loginData')
  const { username } = loginData

  useEffect(() => {
    if (loginData === 'emptyData') {
      history.push("/")
    }
  },[loginData])

  return(
    <div className="content-container">
      <header className="MV-header green-background">
        <div className="MV-header-box-1">
          <Link to="/dashboard">
              <img className="icon-size-1" src={setaVoltarBranca}/>
          </Link>
          <p>{username}</p>
          <Link to="/notifications">
            <img className="icon-size-1" src={sino} />
          </Link>
        </div>
          <div className="MV-header-box-2">
            <img className="icon-size-2 white1-background rounder-icon" src={extratoNeutraBranco} />
            <p className="text-size-1">500 kg</p>
          </div>
      </header>


      <MenuInferior />

    </div>
  )
}

export default withRouter(Neutralization)