/** @format */

import M from 'materialize-css';

const defaultStorage = {
  loginToken: 'empty',
  loginData: 'emptyData',
  };
  
  export const isStorageExists = () => {
    if (typeof Storage !== 'undefined') {
      alert('Seu browser nÃ£o tem suporte para o LocalStorage');
      return false;
    }
    return true;
  };
  
  // Inicia um Storage mas nÃ£o sobrescreve/apaga as keys
  export const initStorage = () => {
    if (!isStorageExists) return;
    Object.entries(defaultStorage).forEach(({ 0: key, 1: value }) => {
      if (!localStorage.getItem(key)) localStorage.setItem(key, JSON.stringify(value));
    });
  };
  
  export const clearStorage = () => {
    if (!isStorageExists) return;
    Object.entries(defaultStorage).forEach(({ 0: key, 1: value }) => {
      localStorage.setItem(key, JSON.stringify(value));
    });
  };
  
  // Retorna um valor de uma chave
  export const getValueByKey = (key) => {
    if (!isStorageExists) return [];
    return JSON.parse(localStorage.getItem(key));
  };
  
  // Salva um valor em uma chave
  export const setValueByKey = (key, value) => {
    if (!isStorageExists) return;
    localStorage.setItem(key, JSON.stringify(value));
  };
  
  // Retorna o Storage como um objeto
  export const getStorageAsObject = () => {
    if (!isStorageExists) return {};
    const result = { ...defaultStorage };
    Object.keys(result).forEach((key) => {
      result[key] = JSON.parse(localStorage.getItem(key)) || result[key];
    });
    return result;
  };

  export const showMessage = (message, type, completeCallback) => {
    let html = '';
    switch(type) {
      default:
      case 'SUCCESS':
        html = `<i class="material-icons left">check</i> ${message}`;
        M.toast({ html, classes: 'success' });
        break;
      case 'ERROR':
        html = `<i class="material-icons left">close</i> ${message}`;
        M.toast({ html, classes: 'error', completeCallback });
        break;
      case 'WARNING':
        html = `<i class="material-icons left">warning</i> ${message}`;
        M.toast({ html, classes: 'warning', completeCallback });
        break;
    }
  } 

  export const sum = (data) => {
    if (data !== undefined) {
      const { wallet: { balance } } = data;
      const sum = balance.reduce((total, type) => (total + type), 0);
      return sum;
    }
  }
  
  export const shuffler = (array) => {
    const array2 = array;
    array2.forEach((item, index) => {
      const novoIndex = Math.floor(Math.random() * (index + 1));
      const temp = item;
      array2[index] = array2[novoIndex];
      array2[novoIndex] = temp;
    });
    return array2;
  };
