import React, { useState } from 'react';
import UserContext from './UserContext';

const UserProvider = (props) => {
  
  const [userName, setUserName] = useState(null);
  const [userCellNumber, setUserCellNumber] = useState(0);
  const [userEmail, setUserEmail] = useState(null);
  const [userCPF, setUserCPF] = useState(0);
  const [userBirthday, setUserBirthday] = useState(0);
  const [userPassword, setUserPassword] = useState('');
  const [userConPassword, setUserConPassword] = useState('');

  /* ainda nao sei se este estado Ã© relevante para a aplicacao */
  const [newUserData, setNewUser] = useState([])
  
  const [justRegistered, setRegistered] = useState(false)

  const setName = (name) => setUserName(name);
  const setCell = (cell) => setUserCellNumber(Number(cell));
  const setEmail = (email) => setUserEmail(email);
  const setCPF = (cpf) => setUserCPF(Number(cpf));
  const setBirthday = (birthday) => setUserBirthday(birthday);
  const setPassword = (password) => setUserPassword(password);
  const setCpassword = (cpassword) => setUserConPassword(cpassword);
  const setNewUserData = (response) => setNewUser(response)
  const setJustRegistered = (state) => setRegistered(state)
  
  const store = {
    setName,
    setCell,
    setEmail,
    setCPF,
    setBirthday,
    setPassword,
    setCpassword,
    setNewUserData,
    setJustRegistered,
    userName,
    userCellNumber,
    userEmail,
    userCPF,
    userBirthday,
    userPassword,
    userConPassword,
    newUserData,
    justRegistered
  };

  return (
    <UserContext.Provider value={store}>
      {props.children}
    </UserContext.Provider>
  );
};

export default UserProvider;