import React from 'react';
import { Link } from 'react-router-dom'; 

export default ({ username, children, to }) =>
  <header style={{ background: 'var(--primary)', flexDirection: 'column' }}>  
    <div style={
      {
        alignItems: 'center',
        display: 'flex',
        justifyContent: 'space-between',
        padding: '16px 8px 0',
        width: '100%',
      }
    }>
      {
        to ? <Link
          to={`${ to }`}
          style={{ display: 'flex', alignItems: 'center', color: 'var(--light-grey)' }}
        >
          <i className="material-icons left">chevron_left</i>
        </Link>:null
      }
      <span style={{ color: 'var(--light-grey)' }} >OlÃ¡, { username }</span>
      <div>
        <Link to="/notifications" style={{ color: 'var(--light-grey)' }}>
          <i className="small material-icons">notifications_none</i>
        </Link>
      </div>
    </div>
    <div style={{ width: '100%', alignText: 'center' }}>
      { children }
    </div>
  </header>;
